.site-header {
  @extend %padding-small;
  background: $brand-color;
  border-bottom: 1px solid $border-color;
  display: inline-block;
  float: left;
  width: 100%;

  a {
    color: $header-link-color;
  }

  .avatar {
    height: 4.5em; // 2em;
    width: 4.5em; // 2em;
    float: left;
    border-radius: 0.2em;
    margin-top: 4px;
    margin-right: 1.1em;
  }

  .site-title {
    float: left;
    font-weight: bold;
    font-size: 2.0em; // 1.0em;
    line-height: 2.0;
    margin-top: 6px; // 8pxx
  }

  .site-nav {
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      line-height: 3.5;
      float: right;
      text-align: right;
      font-size: 1.5em;
      font-weight: bold;
    }
    li {
      display: inline;
      float: left;
      margin-right: 1.0em; // 0.4em;
    }
    @media (max-width: 800px) { // 800px
      ul {
        display: inline-block;
        float: left;
        padding-top: $padding-small;
        text-align: left;
        width: 100%;
      }
    }
  }
}
